import Axios from 'axios'
export default {
  enterRoom: (data) => {
    return new Promise((resolve, reject) => {
      Axios.post('/planningPoker/enterRoom', data).then(
        (response) => {
          resolve(response.data)
        },
        (err) => {
          reject(err)
        }
      )
    })
  }
}
